<template>
  <div id="add-blog">
      <h2>添加博客</h2>
      <form v-if="!submitted" action="">
          <label for="">博客标题</label>
          <input type="text" v-model="blog.title">
            <hr>
          <label for="">博客内容</label>
          <textarea v-model="blog.content" name="" id="" cols="30" rows="10">
          </textarea>
          <div id="checkboxes">
              <label for="">Vue.js</label>
              <input type="checkbox" value="Vue.js" v-model="blog.categories">
              <label for="">Node.js</label>
              <input type="checkbox" value="Node.js" v-model="blog.categories">
              <label for="">React.js</label>
              <input type="checkbox" value="React.js" v-model="blog.categories">
              <label for="">Angular4</label>
              <input type="checkbox" value="Angular4" v-model="blog.categories">
          </div>
          <label for="">作者:</label>
          <select v-model="blog.author" name="" id=""><option v-for="(author,index) in authors" :key="index">{{author}}</option></select>
          <button @click.prevent="post">提交</button>
      </form>

      <div v-if="submitted">您的博客发布成功</div>
      <div id="preview">
          <h3>博客总览</h3>
          <p>博客标题:{{blog.title}}</p>
          <p>博客内容:</p>
          <p>{{blog.content}}</p>
          <p>博客分类:</p>
          <ul>
              <li v-for="(category,index) in blog.categories" :key="index">
                  {{category}}
              </li>
          </ul>
          <p>作者:{{blog.author}}</p>
      </div>
  </div>
</template>

<script>
export default {
    name:'AddBlog',
    data(){
        return{
            blog:{
                title:'',
                content:'',
                categories:[],
                author:'',
            },
            authors:['leo','esther'],
            submitted:false,
        }
    },
    methods:{
        post:function(){
            this.$http.post("https://my-awesome-vuedemo-default-rtdb.firebaseio.com/posts.json",this.blog)
            .then(function(data){
                console.log(data)
                this.submitted=true
            });
        }
    }
}
</script>

<style scoped>
#add-blog *{
    box-sizing: border-box;
  }
  #add-blog{
    margin:20px auto;
    max-width: 600px;
    padding:20px;
  }

  label{
      display:block;
      margin:20px 0 10px;
  }
  
  input[type="text"],textarea,select{
      display:block;
      width:100%;
      padding:8px;
  }

  textarea{
      height:200px;
  }

  #checkboxes input{
      display:inline-block;
      margin-right:10px;
  }

  button{
      display:block;
      margin:20px 0;
      background: crimson;
      color:#fff;
      border:0;
      padding:14px;
      border-radius:4px;
      font-size: 18px;
      cursor:pointer;
  }

  #preview{
      padding:10px 20px;
      border:1px dotted #ccc;
      margin:30px 0;
  }

  h3{
      margin-top:10px;
  }
</style>


