<template>
  <div id="single-blog">
      <h1>博客内容</h1>
        <article>
          {{blog.content}}
        </article>
        <p>作者:{{blog.author}}</p>
        <p>分类:</p>
        <ul>
            <li v-for="category in blog.categories" :key="category.id">
                {{category}}
            </li>
        </ul>
  </div>
</template>

<script>

export default {
    name:'single-blog',
    data(){
        return{
            id:this.$route.params.id,
            blog:{}
        }
    },
    created(){
        this.$http.get('https://my-awesome-vuedemo-default-rtdb.firebaseio.com/posts/'+this.id+'.json') 
        .then(function(data){
            console.log(data);
            return data.json();
      //this.blog = data.body;
        })
        .then(function(data){
            this.blog= data;
        })
    }
}
</script>

<style>
#single-blog{
    max-width: 960px;
    margin: 0 auto;
    padding:20px;
    background: #eee;
    border:1px dotted #aaa;
}
</style>


