<template>
  <div v-theme:column="'width'" id="show-blogs">
    <h1>博客总览</h1>
    <input type="text" v-model="search" placeholder="搜索" />
    <div id="single-blog" v-for="(blog) in filterBlogs" :key="blog.id">
      <router-link v-bind:to="'/blog/'+blog.id">
      <h2 v-rainbow>{{ blog.title | to-uppercase }}</h2>
      </router-link>
      <article>
        {{ blog.content }}
      </article>
    </div>
  </div>
</template>

<script>
export default {
  name: "show-blogs",
  data() {
    return {
      blogs: [],
      search: "",
    };
  },
  created() {
    this.$http
      .get("https://my-awesome-vuedemo-default-rtdb.firebaseio.com/posts.json")
      .then(function (data) {
        return data.json();
        //this.blogs = data.body.slice(0, 10);
      })
      .then(function(data){
        var blogsArray = [];
        for(let key in data){
          data[key].id=key;
          blogsArray.push(data[key]);
        }
        this.blogs = blogsArray;
      })
  },
  computed: {
    filterBlogs: function () {
      return this.blogs.filter((blog) => {
        return blog.title.match(this.search);
      });
    },
  },
  filters: {
    "to-uppercase":function(value){
      return value.toUpperCase();
    },
  },
  directives:{
    'rainbow':{
      bind(el,binging,vnode){
        el.style.background = "#"+Math.random().toString(16).slice(2,8);
        
      }
    }
  }
};
</script>

<style>
#show-blogs {
  max-width: 800px;
  margin: 0 auto;
}

.single-blog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: grey;
  border:1px dotted #aaa;
}
 #show-blogs a{
  color:#444;
  text-decoration:none;
}

input[type="text"]{
  padding:8px;
  width:100%;
  box-sizing:border-box;
}

</style>


